<template>
  <div class="sel-resolution">
    <vx-header :showback="1" :showsave="1" :title="$t('scenarist.saveTitle')" @save="save"></vx-header>
    <!-- 场景特效列表 -->
    <div class="vx-container">
      <div class="vx-scene_list">
        <div class="vx-scene_item" :class="{selected:scene.id == selected.id}" v-for="(scene,index) in scenes" :key="scene.id" @click="selecteds(scene, index)">
          <div :class="{selecting:scene.id == selecting}"></div>
          <div class="no" :class="{online:scene.sceneStatus}">
            {{scene.id + 1}}
          </div>
          <button class="operator">
            {{scene.name}}
          </button>
        </div>
      </div>
    </div>
    <vx-conform v-if='conform' @cancelConform='cancelConform' @sureConform='sureConform' :message='message'></vx-conform>
  </div>
</template>
<script>
  import htp from "../../api/http";
  import urls from '../../api/apiurl';
  import { Toast, Indicator } from 'mint-ui';
  export default {
    data() {
      return {
        conform: false,
        message: '',
        scenes: [],
        selected: {},
        selecting: ''
      };
    },
    mounted() {
      this.getScenes();
      this.getCurScene();
    },
    methods: {
      getScenes() {
        htp.get(urls.getSceneList).then(res => {
          if(res.status === 10000000) {
            this.scenes = res.data.sceneList;
          }
        });
      },
      getCurScene() {
        htp.get(urls.getCurScene).then(res => {
          if(res.status === 10000000) {
            this.selecting = res.data.sceneCur;
          }
        });
      },
      selecteds(scene, index) {
        this.selected = scene;
        this.selectIndex = index;
      },
      cancelConform() {
        this.conform = false;
      },
      sureConform() {
        if(this.selected.hasOwnProperty('id')) {
          htp.post(urls.saveScene, { sceneId: this.selected.id });
          this.$router.go(-1);
        }
      },
      save() {
        let scenceStatus = this.scenes.filter(item => {
          return item.sceneStatus === 1;
        });
        let flag = scenceStatus.some(item => {
          return item.id === this.selected.id;
        });
        if(flag) {
          this.conform = true;
          this.message = this.$t('scenes.SecondaryConfirmation') + '"' + this.scenes[this.selected.id].name + '"';
        } else {
          if(this.selected.hasOwnProperty('id')) {
            htp.post(urls.saveScene, { sceneId: this.selected.id });
            this.$router.go(-1);
          } else {
            Toast(this.$t('common.selectedScene'));
          }
        }

      }
    }
  };
</script>

<style lang="less" scoped>
  .active {
    background: #2e2e34 !important;
  }
  .selecting {
    position: absolute;
    width: 25px;
    height: 25px;
    background: #fd7b00;
    border-radius: 50%;
    top: 15px;
    left: 15px;
  }
  .vx-scene {
    height: 100%;
    &_list {
      display: flex;
      flex-wrap: wrap;
      padding-bottom: 20px;
      overflow: auto;
      .vx-scene_item {
        position: relative;
        background-color: #6b7082;
        box-shadow: 2px 2px 5px #171719;
        border: 2px solid #212124;
        box-sizing: border-box;
        border-radius: 10px;
        // height: 400px;
        .no {
          text-align: center;
          color: #ddd;
        }
        .online {
          background-color: #3699f9;
          border-top-left-radius: 10px;
          border-top-right-radius: 10px;
        }
        .operator {
          border-top: 1px solid #2b2c33; /*no*/
          background-color: #40434c;
          height: 35%;
          vertical-align: middle;
          line-height: 100%;
          width: 100%;
          border-bottom-left-radius: 10px;
          border-bottom-right-radius: 10px;
          text-align: center;
          color: #ddd;
          border: 0;
          font-size: 24px;
          &:active {
            border: 0;
          }
        }
      }
      .selected {
        border: 2px solid #fd7b00; /*no*/
        border-radius: 10px;
      }
    }
  }

  /*竖屏模式*/
  @media screen and (orientation: portrait) {
    .vx-scene {
      &_list {
        margin: 35px 35px 0 35px;
        // padding: 0 60px;
        justify-content: space-between;
        .vx-scene_item {
          width: 30%;
          margin-bottom: 25px;
          height: 150px;
          .no {
            height: 65%;
            line-height: 130px;
            font-size: 20px; /*no*/
          }
        }
      }
    }
  }
  /*横屏模式*/
  @media screen and (orientation: landscape) {
    .vx-scene {
      &_list {
        // margin: 30px 35px;
        overflow: auto;
        display: flex;
        // justify-content: space-between;
        padding: 40px;
        .vx-scene_item {
          width: 22%;
          height: 150px;
          margin-right: 4%;
          margin-bottom: 25px;
          &:nth-child(4n) {
            margin-right: 0;
          }
          .no {
            height: 65%;
            line-height: 95px;
            font-size: 20px; /*no*/
          }
        }
      }
    }
  }
</style>




